}
body
{
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 14px;
    line-height: 1.42857143;

    color: #333;
    background-color: #fff;
}
input,
button,
select,
textarea
{
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}
a
{
    text-decoration: none;

    color: #337ab7;
}
a:hover,
a:focus
{
    text-decoration: underline;

    color: #23527c;
}
a:focus
{
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
}
figure
{
    margin: 0;
}
img
{
    vertical-align: middle;
}
.img-responsive,
.thumbnail > img,
.thumbnail a > img,
.carousel-inner > .item > img,
.carousel-inner > .item > a > img
{
    display: block;

    max-width: 100%;
    height: auto;
}
.img-rounded
{
    border-radius: 6px;
}
.img-thumbnail
{
    line-height: 1.42857143;

    display: inline-block;

    max-width: 100%;
    height: auto;
    padding: 4px;

    -webkit-transition: all .2s ease-in-out;
         -o-transition: all .2s ease-in-out;
            transition: all .2s ease-in-out;

    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: #fff;
}
.img-circle
{
    border-radius: 50%;
}
hr
{
    margin-top: 20px;
    margin-bottom: 20px;

    border: 0;
    border-top: 1px solid #eee;
}
.sr-only
{
    position: absolute;

    overflow: hidden;
    clip: rect(0, 0, 0, 0);

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;

    border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus
{
    position: static;

    overflow: visible;
    clip: auto;

    width: auto;
    height: auto;
    margin: 0;
}
